import React, { useEffect, useState } from "react";
import ArrowBackIosIcon from "@mui/icons-material/ArrowBackIos";
import DriveFileRenameOutlineIcon from "@mui/icons-material/DriveFileRenameOutline";
import { useNavigate, useSearchParams } from "react-router-dom";
import { CloseRounded } from "@mui/icons-material";
import Backdrop from "@mui/material/Backdrop";
import Box from "@mui/material/Box";
import Modal from "@mui/material/Modal";
import Fade from "@mui/material/Fade";
import Typography from "@mui/material/Typography";
import CheckBoxOutlineBlankIcon from "@mui/icons-material/CheckBoxOutlineBlank";
import "./Seats.scss";
import fisrtImg from '../img/how much seat/bicycle.avif';
import secImg from '../img/how much seat/2.bike.avif';
import thirdImg from "../img/how much seat/auto.avif";
import fourImg from '../img/how much seat/4.small-car.avif';
import fivImg from '../img/how much seat/5.car.avif';
import sixImg from '../img/how much seat/6.car.avif';
import sevenImg from '../img/how much seat/7-8-van.avif';
import tenImg from '../img/how much seat/9-10-bus.avif';

const Seats = () => {
  let navigate = useNavigate();
  const [seatOpen, setSeatOpen] = React.useState(true);
  const handleSeatsClose = () => setSeatOpen(false);
  const handleSeatsOpen = () => setSeatOpen(true);
  const [bookingSeat, setBookingSeat] = useState(2);
  const [amount, setAmount] = useState();
  const [seatData,setSeatData] = useState(); 
  const [showDiffImg, setshowDiffImg] = useState("");
  const [userSeat,setUserSeat] = useState(1)
  // console.log(useState)

//previwes page data
  const [param] = useSearchParams();
  let geMovieData = JSON.parse(param.get("obj"));
  let getCurrTheater = JSON.parse(param.get("currObj"));
  let getShowTimeData = param.get("showTime");

//how much seat model box
  const style = {
    position: "absolute",
    top: "50%",
    left: "50%",
    transform: "translate(-50%, -50%)",
    width: 600,
    bgcolor: "background.paper",
    borderRadius: 3,
    boxShadow: 24,
    p: 4,
  };
  

//Seats
  let getSeat = getCurrTheater.seats;

  useEffect(() => {
    getSeats(); 
    setshowDiffImg({id:2,img:secImg});
  }, []);

  const getSeats = () => { 
    const temp = Array.from(Array(getSeat[0].total)).map((c, i) => { 
      return { 
        num: i + 1, 
        selected: false, 
        isBooked: getSeat[0].booked.includes(i + 1), 
      }; 
    }); 
    setSeatData(temp); 
  }; 

  const handleSelect = (num, arr, ticketCount) => { 
    if (ticketCount === 0) {
      setSeatData(arr);
        } 
    else{
        const temp = [...arr].map((a, i) => { 
          if (num === a.num && !a.isBooked) {
            return { ...a, selected: !a.selected, }; 
            } else return a; 
          }); 
        handleSelect(num + 1, temp, ticketCount - 1); 
        document.querySelector('.seatStatus').style.display = 'none'
        document.querySelector('.payDiv').style.display = 'flex';
        if(num<=250){
          setAmount(190*ticketCount)
        }
        else if(num>250){
          setAmount(60*ticketCount)
        }
        }
  }; 

//seat prv page nav
  const handlePrvNavigate = () => {
    let obj = JSON.stringify(geMovieData);
    // console.log(obj)
    navigate(`/TheaterShow?data=${obj}`);
  };

//How Many Seat Selection
  let imgArray = [
    {
      id : 1,
      img : fisrtImg
    },
    {
      id : 2,
      img : secImg
    },
    {
      id : 3,
      img : thirdImg
    },
    {
      id : 4,
      img : fourImg
    },
    {
      id : 5,
      img : fivImg
    },
    {
      id : 6,
      img : sixImg
    },
    {
      id : 7,
      img : sevenImg
    },
    {
      id : 8,
      img : sevenImg
    },
    {
      id : 9,
      img : tenImg
    },
    {
      id : 10,
      img : tenImg
    }];

  const handleHowManySeat = (e,i) => {
    setBookingSeat(e.id);
    setshowDiffImg(e);
    setUserSeat(i)

  };

//navigate next page
  const handleNavigate = () => {
    let obj = JSON.stringify(geMovieData);
    let theater = JSON.stringify(getCurrTheater);
    let showDetail = JSON.stringify(getShowTimeData);

    //get the seat number for user
    const handleSeatNumber = seatData.filter((e, i) => {
      return e.selected === true ? e.num : e.selected;
    });
    // console.log(handleSeatNumber);
    navigate(
      `/buyTicket?obj=${obj}&theater=${theater}&showDetail=${showDetail}&amount=${amount}&seatNumber=${JSON.stringify(
        handleSeatNumber
      )}`
    );
  };

return (
    <>
      <div
        style={{ backgroundColor: "rgb(51, 53, 69)", color: "#fff" }}
        className="d-flex justify-content-between p-2 align-items-center position-sticky top-0 left-0"
      >
        <div className="d-flex text-start align-items-center ">
          <ArrowBackIosIcon
            style={{ cursor: "pointer" }}
            className="fs-2 cursor-poniter"
            onClick={handlePrvNavigate}
          />
          <div>
            <h4>
              {geMovieData.Title}{" "}
              <small
                style={{
                  border: "1px solid",
                  width: "30px",
                  height: "30px",
                  borderRadius: "50%",
                }}
                className="fs-6 p-1"
              >
                {geMovieData.ceritificate}
              </small>
            </h4>
            <p style={{ fontSize: "14px", color: "#ccc" }}>
              {getCurrTheater.tName} | {getShowTimeData}
            </p>
          </div>
        </div>
        <div className="d-flex d-sm-flex">
          <p
            style={{ borderRadius: "8%", cursor: "pointer", fontSize: "14px" }}
            onClick={handleSeatsOpen}
            className="p-1 border"
          >
            {bookingSeat} Tickets
            <DriveFileRenameOutlineIcon />
          </p>{" "}
          <span style={{ cursor: "pointer" }} onClick={handlePrvNavigate}>
            <CloseRounded className="fs-2" />
          </span>
        </div>
      </div>
{/* TheaterSeats */}     
    <div>
        <p className="w-75 m-auto text-secondary text-start mt-4 mb-2">DIAMOUND 190</p>
        <div style={{ display: "flex", flexDirection: "row",flexWrap: "wrap", width:'75%',margin:'auto'}}>
        {
          seatData?.map((e,index) => {
            if(e.num<=250){
              return( 
                <div key={index} 
                  onClick={() => 
                    !e.isBooked && 
                    handleSelect(
                      e.num, 
                    seatData.map((c) => {
                      return { 
                        ...c,
                      selected: false,
                      }
                    }),
                    bookingSeat
                  )} 
                  className={`DOM-Seats ${e.isBooked ? "seat-booked" : ""} ${e.selected ? "seat-selected" : ""}`}
                  >{e.num}
                  </div> 
                )
            }
              }
            )
          }
        </div>

        <p className="w-75 m-auto text-secondary text-start mt-4 mb-2">PEARL 60</p>
        <div style={{ display: "flex", flexDirection: "row",flexWrap: "wrap", width:'75%',margin:'auto'}}>
        {
          seatData?.map((e,index) => {
            if(e.num>250){
              return( 
                <div key={index} 
                  onClick={() => 
                    !e.isBooked && 
                    handleSelect(
                      e.num, 
                    seatData.map((c) => {
                      return { 
                        ...c,
                      selected: false,
                      }
                    }),
                    bookingSeat
                  )} 
                  className={`DOM-Seats ${e.isBooked ? "seat-booked" : ""} ${e.selected ? "seat-selected" : ""}`} 
                  >{e.num}
                  </div> 
                )
            }
              }
            )
          }
        </div>

      <div className="theater-screen">
      <span className="screen-display">
        <span className="screen-display-small">
          <img alt="" className="screen-img" aria-hidden="true" src="data:image/svg+xml,%3csvg%20xmlns=%27http://www.w3.org/2000/svg%27%20version=%271.1%27%20width=%27375%27%20height=%2744%27/%3e"/>
        </span>
        <img alt="Movies Screen Icon" className="movie-screen-icon" src="https://assetscdn1.paytm.com/movies_new/_next/static/media/screen-icon.8dd7f126.svg" decoding="async" data-nimg="intrinsic" srcset="https://assetscdn1.paytm.com/movies_new/_next/static/media/screen-icon.8dd7f126.svg 1x, https://assetscdn1.paytm.com/movies_new/_next/static/media/screen-icon.8dd7f126.svg 2x"/>
      </span>
      </div>
    </div>
{/* Seat Status */}
      <div
        className="seatStatus w-100 justify-content-center p-1 bg-light position-sticky bottom-0 end-0"
        style={{ display: "flex" }}
      >
        <p className="p-2">
          <CheckBoxOutlineBlankIcon style={{ color: "rgb(238, 167, 36)" }} />{" "}
          Bestseller
        </p>
        <p className="p-2">
          <CheckBoxOutlineBlankIcon style={{ color: "#1ea83c" }} /> Available
        </p>
        <p className="p-2">
          <CheckBoxOutlineBlankIcon
            style={{ backgroundColor: "#1ea83c", color: "#1ea83c" }}
          />{" "}
          Selected
        </p>
        <p className="p-2">
          <CheckBoxOutlineBlankIcon
            style={{ backgroundColor: "#ccc", color: "#ad9ea1" }}
          />{" "}
          Sold
        </p>
      </div>
{/* Tickets Amount */}
      <div
        className="payDiv w-100 justify-content-center bg-light position-sticky bottom-0 end-0"
        style={{ display: "none" }}
      >
        <button
          className="payBtn w-25"
          onClick={handleNavigate}
        >
          Pay RS {amount}
        </button>
      </div>
{/* HowMuchSeat */}
      <div className="how-many-seat">
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          open={seatOpen}
          closeAfterTransition
          slots={{ backdrop: Backdrop }}
          slotProps={{
            backdrop: {
              timeout: 500,
            },
          }}
        >
          <Fade in={seatOpen}>
            <Box sx={style}>
              <Typography
                id="transition-modal-title"
                variant="h6"
                component="h2"
              >
                <p>How Many Seat?</p>
              </Typography>
              <Typography id="transition-modal-description" sx={{ mt: 2 }}>
                <div className="seat-img mb-5">
                  <img src={showDiffImg.img} alt="Seat img Loading..." />
                </div>
                <div  className="howMuchSeats-img">
                {
                  imgArray.map((e,i)=>{
                    return(
                        <div key={i} className="howMuchSeats">
                          <p className={`totalSeats ${userSeat === i?"seat-selected":null}`}
                            onClick={()=>handleHowManySeat(e,i)}
                            >
                              {e.id}
                          </p>
                        </div>
                    )
                  })
                }
                </div>
                
                <div className="ticket-price-detail">
                  <div className="ticket-diamound">
                    <p>DIAMOUND</p>
                    <h4>RS.190.00</h4>
                    <p style={{ color: "rgb(0, 207, 0)" }}>Available</p>
                  </div>
                  <div className="ticket-pearl">
                    <p>PEARL</p>
                    <h4>RS.60.00</h4>
                    <p style={{ color: "#8b8b8b" }}>Solid Out</p>
                  </div>
                </div>
                <div className="select-seat">
                  <button
                    className="select-seats-btn"
                    onClick={handleSeatsClose}
                  >
                    Select Seats
                  </button>
                </div>
              </Typography>
            </Box>
          </Fade>
        </Modal>
      </div>
    </>
  );
};

export default Seats;
