import React, { useReducer } from 'react';
import { BrowserRouter, Route, Routes } from 'react-router-dom';
import BodyTop from './Home/Body-Top';
import { ConxtCreate, Reducer, movies } from './Reducer';
import MoviesDetail from './BackPage/MoviesDetail';
import TheaterShow from './Theaters/Theaters';
import Seats from './Seats/Seats';
import BuyTicket from './BuyTicket/BuyTicket';

const Routing = () => {
    const [data,dispatch] = useReducer(Reducer,movies);
  return (
    <ConxtCreate.Provider value={{data,dispatch}}>
        <BrowserRouter>
            <Routes>
                <Route path='/' element={<BodyTop/>}/>
                <Route path='/MovieDetail' element={<MoviesDetail/>}/>
                <Route path='/TheaterShow' element={<TheaterShow/>}/>
                <Route path='/theater-seats' element={<Seats/>}/>
                <Route path='/buyTicket' element={<BuyTicket/>}/>
                <Route path='*' element={<BodyTop/>}/>
            </Routes>
        </BrowserRouter>
    </ConxtCreate.Provider>
  )
}

export default Routing