import React, { useContext } from 'react'
import { ConxtCreate } from '../Reducer'
import './Body-Top.scss'
import Slider from "react-slick";
import { useNavigate } from 'react-router-dom';
import Nav from '../Navbar/Nav';
import Carosal from '../Carosal/Carosal';
import Footer from '../Footer/Footer';

const BodyTop = () => {
  const {data,dispatch} = useContext(ConxtCreate);
  const moviesCard = data.MoviesData.movies;
  const primeMovies = data.MoviesData.Premieres;
  
//carosal
  const settings = {
    infinite: true,
    speed: 500,
    slidesToShow: 5,
    slidesToScroll: 5,
    responsive: [
      {
        breakpoint: 960,
        settings: {
          slidesToShow: 3,
          slidesToScroll: 1,
          fontSize:"1rem"
        }
      },
      {
        breakpoint: 620,
        settings: {
          slidesToShow: 1,
          slidesToScroll: 1,
          fontSize:"1rem"
        }
      }
    ]
  };

//next page pass the data
  const navigate = useNavigate()
  const handleData = (e) => {
    let data = JSON.stringify(e)
    navigate(`/MovieDetail?movidata=${data}`)
  }

  return (
    <>
    <Nav/>
    <Carosal/>
    <div className='container'>  
      <h2>Recommended Movies</h2>
      <div className='movie-card'>
      <Slider {...settings}>
        {
          moviesCard?.map((e,i)=>{
            return( 
            <div key={i} className='cards' onClick={()=>handleData(e)}>
              <div className='card-img'>
                <img src={e.img} alt='Loading...'/>
              </div>
              <h4>{e.Title}</h4>
              <p>{e.Genre}</p>
            </div>
            )
          })
        }
        </Slider>
        </div>
    </div>
    <div className='prime-container'>  
          <h2>Premieres Movies</h2>
          <div className='prime-card'>
          <Slider {...settings}>
            {
              primeMovies.map((e,i)=>{
                return( 
                <div key={i} className='prime-cards' onClick={()=>handleData(e)}>
                  <div className='prime-card-img'>
                    <img src={e.img} alt='Loading...'/>
                  </div>
                  <h4>{e.Title}</h4>
                  <p>{e.Genre}</p>
                </div>
                )
              })
            }
          </Slider>
          </div>
    </div>
    <Footer/>
    </>
  )
}

export default BodyTop