import React from "react";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
import firstImg from '../img/Home-carosal/first-photo.avif';
import secondImg from '../img/Home-carosal/second-photo.avif';


export default function carosal(){
    const settings = {
        className: "center",
        centerMode: true,
        infinite: true,
        slidesToScroll: 1,
        slidesToShow: 1,
        speed: 500,
        autoplay:true,
        autoplaySpeed: 1500,
        responsive: [
          {
            breakpoint: 1000,
            settings: {
              centerPadding : "80px",
            }
          },
          {
            breakpoint: 620,
            settings: {
              centerPadding : "0.2rem",
            }
          },
          {
            breakpoint: 320,
            settings: {
              centerPadding : "0.2rem",
            }
          }
        ]
    }
    return (
      <div>
        <div style={{marginTop:'20px',overflow:'hidden'}}>
        <Slider {...settings} className="carosalMedia">
          <div style={{width:'100%'}}>
            <img src={firstImg} alt="Loading..." style={{width:'99%',borderRadius:'5px'}}/>
          </div>
          <div style={{width:'100%'}}>
            <img src={secondImg} alt="Loading..." style={{width:'99%',borderRadius:'5px'}}/>
          </div>
        </Slider>
        </div>
      </div>
    );
  }
